import React, { Component } from 'react'
// import './control.css'

class Control extends Component {
  constructor() {
    super();
    this.state={
      inpValue:'',
      todos:[
			// {title:'1',done:false},
			// {title:'2',done:true},
			// {title:'3',done:true},
			// {title:'4',done:false},
			]
    }
  }
  handleChange=(e)=>{
    console.log(e.target.value);
    this.setState({inpValue:e.target.value});
  }
  componentDidMount(){
		this.input.focus();
		let todos = localStorage.getItem('todos');
		if(todos){
			this.setState({
				todos:JSON.parse(todos)
			})
		}
	}
	componentDidUpdate(){
		localStorage.setItem('todos',JSON.stringify(this.state.todos));
	}
  addTodo = ()=>{
    // this.state.todos.push({title:this.state.inpValue})
		// this.setState({todos:this.state.todos})
		if(!this.state.inpValue){
			alert('不能为空');
		}else{
			this.setState({
				todos:[
					{
						title:this.state.inpValue,
						done:false
					},
					...this.state.todos
				]
			})
			this.setState({inpValue:''});
		}
  }
  deleteTodo = (index)=>{
		// let newTodos = JSON.parse(JSON.stringify(this.state.todos));
		// newTodos.splice(index,1);
		// this.setState({todos:newTodos});

		this.setState({
			todos:this.state.todos.filter((item,idx)=>idx != index)
		})
		//setState是异步执行的，注意callback的用法
		// this.setState({
		// 	todos:this.state.todos.filter((item,idx)=>idx != index)
		// },()=>{
		// 	localStorage.setItem('todos',JSON.stringify(this.state.todos));
		// })
	}
	toggle = (index)=>{
		let newTodos = JSON.parse(JSON.stringify(this.state.todos));
		newTodos[index].done = !newTodos[index].done;
			this.setState({
			todos:newTodos
		})
	}
	renderTodos = (done)=>{
		return this.state.todos.map((todo,index)=>{
			if(done == todo.done){
				return <li>
					<input 
					checked={done}
					onClick={()=>this.toggle(index)}
					type="checkbox"
					/>
					<span dangerouslySetInnerHTML={{__html:todo.title}}></span>
					<button onClick={()=>this.deleteTodo(index)}>删除</button>
				</li>
			}
		})
	}
  render() {
		// localStorage.setItem('todos',JSON.stringify(this.state.todos));
		const {todos,inpValue} = this.state;
		// let arr1 = todos.filter(item=>!item.done)
		// console.log(arr1);
    return (	
	    <div className='box'>
				<label htmlFor="inp">todolist</label>
        <input
          ref={inp=>this.input = inp}
          type="text"
          value={inpValue}
          onChange={this.handleChange}
        />
        <button onClick={this.addTodo}>添加</button>
				<h2>正在进行</h2>
        <ul>
          {this.renderTodos(false)}
        </ul>  
				<h2>已完成</h2>  
				<ul style={{color:1>2?'blue':'orange'}}>
					{this.renderTodos(true)}
				</ul>

				{/* <button>{1<2?'登录':'注册'}</button>       */}
      </div>
    )
  }
}

export default Control;
